             </p>
             <p>
               和着水波潋滟的浮沉，剪下一段心情，听那云烟深处，此时的岸此时的景，千流万壑，孤吟寒水中，遥遥回眸不禁问诗人：馀音空中尚飘荡，不知人在否!
             </p>
             <p>
               彼岸花开，生生相错，唯有这绕耳的笛声还在诉说着凄美故事。碧波湖畔，红桃绿柳，盈盈一水，脉脉无语，不知这样的无奈横亘了多久，笛声轻起，比相思更浓，比忧怨更深，自笛声飘起的那一刻，便记载和传递了千年后物是人非的牵牵绊绊。
             </p>
             <p class="imgtext">
