             </p>
             <p>
               笛当是紫竹做，和着梅花的清傲与竹虚柔之韵，缥缥缈缈的清音柔滑如少女的纤手，牵着你穿越溪流，穿越红尘，停留在你记忆中的某个瞬间，直至入心入骨。笛中曲，月下人，月不落，笛岂终，斯境中，这一刻，听的人，歌之人，谁又分得清谁在月外谁又在月中，只看见那细按香盈的手，漾出一脉清流，只那么静静的远远的吹过来，不由人感叹：月下笛吹，吹净遥碧已无声…… ——清风明月
             </p>
